import React, { useState } from "react";
import Card from "../UI/Card";
import Button from "../UI/Button";
import classes from "./AddUser.module.css";
import Wrapper from "../Helpers/Wrapper";
import ErrorModal from "../UI/ErrorModal";

const AddUser = (props) => {

  const [enteredUsername, setEnteredUsername] = useState("");
  const [enteredAge, setEnteredAge] = useState("");
  const [error, setError] = useState();

  const addUserHandler = (event) => {
    event.preventDefault();
    if(enteredUsername.trim().length === 0 && enteredAge.trim().length === 0){
        setError({
            title: 'Invalid Input',
            message: 'Please enter a valid name and age (non-empty values).'
        });
        return;
    }
    if(enteredUsername.trim().length === 0){
        setError({
            title: 'Invalid Input',
            message: 'Please enter a valid name (non-empty value).'
        });
        return;
    }
    if(enteredAge.trim().length === 0){
        setError({
            title: 'Invalid Input',
            message: 'Please enter a valid age (non-empty value).'
        });
        return;
    }
    if(+enteredAge < 1){
        setError({
            title: 'Invalid Age',
            message: 'Please enter a valid age (greater than 0).'
        });
        return;
    }
    if(+enteredAge > 120){
        setError({
            title: 'Invalid Age',
            message: 'Please enter a valid age (It seems to be too high).'
        });
        return;
    }
    props.onAddUser(enteredUsername, enteredAge);
    setEnteredUsername("");
    setEnteredAge("");
  };

  const usernameChangeHandler = (event) => {
    setEnteredUsername(event.target.value);
  };

  const ageChangeHandler = (event) => {
    setEnteredAge(event.target.value);
  };

  const errorHandler = () => {
      setError(null);
  }

  return (
      <Wrapper>
    
    {/* Or we can say its a condition
    {error ? <ErrorModal title="An error occured!" message="Something went wrong!" /> : ''} */}
    
    {error && <ErrorModal title={error.title} message={error.message} onConfirm={errorHandler}/> }
    <Card className={classes.input}>
      <form onSubmit={addUserHandler}>
        <label htmlFor="username">Username</label>
        <input
          type="text"
          value={enteredUsername}
          id="username"
          onChange={usernameChangeHandler}
        />
        <label htmlFor="age">Age</label>
        <input
          type="number"
          value={enteredAge}
          id="age"
          onChange={ageChangeHandler}
        />
        <Button type="submit">Add User</Button>
      </form>
    </Card>
    </Wrapper>
  );
};

export default AddUser;
